export default {
	data() {
		return {
			title: 'Hello',
			phone:""
		}
	},
	onLoad(option) {
		if(option.phone){
			this.phone = option.phone
			// this.getData();
		}else{
			uni.showModal({
				showCancel:false,
				title:"未获取到手机号码",
				content:"按确定后，将返回生成页。",
				success:function(res){
					if(res.confirm){
						uni.navigateBack({
							delta:3
						})
					}
				}
			})
		}
	},
	methods: {
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		buy(){
			this.$ajax("/virtual/buy","POST",{phone:this.phone}).then((res)=>{
				console.log(res);
				if(res.code=='200'){
					
					uni.requestPayment({
						provider: "wxpay",
						timeStamp:res.paymentResult.payment.timeStamp,
						nonceStr:res.paymentResult.payment.nonceStr,
						package:res.paymentResult.payment.package,
						signType:res.paymentResult.payment.signType,
						paySign:res.paymentResult.payment.paySign,
						success:function(){
							uni.showToast({
								"title":"支付成功",
								"icon":"none"
							})
							uni.navigateBack({
								delta:3
							})
						},
						fail:function(){
							uni.showToast({
								"title":"取消支付",
								"icon":"none"
							})
						}
					})
				}else{
					uni.showToast({
						"title":"服务器错误，请稍后再试"
					})
				}
			})
		}
	}
}
